/* unvisited link */
a:link {
  color: #00CC97;
 
}

/* visited link */
a:visited {
  color: #00CC97;
}

/* mouse over link */
a:hover {
  color: #00BDFF;
}

/* selected link */
a:active {
  color: #00CC97;
}

